/**
 * Global Variables
 */

/* Breakpoint Screen Sizes */
$screen-bp-min	: 320px;	// content shouldn't get below this width
$screen-bp-sm	: 750px;
$screen-bp-md	: 1270px;
$screen-bp-lg	: 1600px;	// content shouldn't get above this width

/* Breakpoint Font Root Sizes */
$text-bp-min	: 16px;		// fonts stay this size and will not scale under screen-bp-min
$text-bp-sm		: 16px;		// fonts will scale up relative to screen-bp-sm
$text-bp-md		: 12px;		// fonts will scale up relative to screen-bp-md
$text-bp-lg		: 10px;		// fonts will scale up relative to screen-bp-lg then stay there

/* Colors */
$color-1	: #FFFFFF;	// white
$color-2	: #000000;	// black

$overlay-bg	: rgba(0,0,0,0.95);	// semi-transparent black


